<template>
  <div
    class="modal-dialog modal-dialog-centered modal-dialog-scrollable modal-sm"
  >
    <div class="modal-content">
      <!-- 產品名 -->
      <div class="modal-header">
        <h5 class="modal-title fs-4" id="exampleModalLabel">
          {{ sentData.productName }}
        </h5>
        <button
          type="button"
          class="btn-close"
          data-bs-dismiss="modal"
          aria-label="Close"
        ></button>
      </div>
      <!-- 產品資料 -->
      <div class="modal-body">
        <!-- <div class="py-2">
          <span class="">訂購人姓名</span>
          <input class="form-control mt-1" type="text" />
        </div> -->
        <!-- 杯型 -->
        <div class="py-2">
          <p><span class="text-danger">✽</span><span>杯型</span></p>
          <div>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="largeSize"
                value="largeSize"
                name="cupSize"
                v-model="pickOption.whatCup"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="largeSize"
                >大杯</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="mediumSize"
                value="mediumSize"
                name="cupSize"
                v-model="pickOption.whatCup"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn"
                for="mediumSize"
                >中杯</label
              >
            </span>
          </div>
        </div>
        <!-- 溫度 -->
        <div class="py-2">
          <p><span class="text-danger">✽</span><span>溫度</span></p>
          <div class="d-flex flex-wrap">
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="iceFull"
                value="iceFull"
                v-model="pickOption.whatTemp"
                name="temperature"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn"
                for="iceFull"
                >正常冰</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="iceFew"
                value="iceFew"
                v-model="pickOption.whatTemp"
                name="temperature"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn"
                for="iceFew"
                >少冰</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="iceLittle"
                value="iceLittle"
                v-model="pickOption.whatTemp"
                name="temperature"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="iceLittle"
                >微冰</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="iceNone"
                value="iceNone"
                v-model="pickOption.whatTemp"
                name="temperature"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="iceNone"
                >去冰</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="nomralTemp"
                value="nomralTemp"
                v-model="pickOption.whatTemp"
                name="temperature"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="nomralTemp"
                >常溫</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="hotTemp"
                value="hotTemp"
                v-model="pickOption.whatTemp"
                name="temperature"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="hotTemp"
                >熱</label
              >
            </span>
          </div>
        </div>

        <!-- 糖度
         -->
        <div class="py-2">
          <p><span class="text-danger">✽</span><span>糖度</span></p>
          <div class="d-flex flex-wrap">
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="sugarFull"
                value="sugarFull"
                v-model="pickOption.whatSugar"
                name="sugarHowMany"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="sugarFull"
                >正常糖</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="sugarFew"
                value="sugarFew"
                v-model="pickOption.whatSugar"
                name="sugarHowMany"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="sugarFew"
                >少糖</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="sugarHalf"
                value="sugarHalf"
                v-model="pickOption.whatSugar"
                name="sugarHowMany"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="sugarHalf"
                >半糖</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="sugarLittle"
                value="sugarLittle"
                v-model="pickOption.whatSugar"
                name="sugarHowMany"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="sugarLittle"
                >微糖</label
              >
            </span>
            <span class="m-1">
              <input
                type="radio"
                class="btn-check"
                id="sugarNone"
                value="sugarNone"
                v-model="pickOption.whatSugar"
                name="sugarHowMany"
              />
              <label
                class="btn btn-outline-warning text-dark optionBtn optionBtn"
                for="sugarNone"
                >無糖</label
              >
            </span>
          </div>
        </div>
      </div>
      <!-- 資料送出 -->
      <div class="modal-footer">
        <div>
          <div class="input-group">
            <button class="btn btn-outline-secondary" v-on:click="numMinus">
              -
            </button>
            <input
              type="text"
              class="form-control text-align border-secondary"
              style="width: 45px"
              v-model="orderDesc.orderNum"
            />
            <button class="btn btn-outline-secondary" v-on:click="numPlus">
              +
            </button>
          </div>
        </div>
        <div>總金額：{{ totalMoney() }} 元</div>
        <div></div>
        <button
          id="checkBtn"
          type="button"
          class="btn btn-dark text-white"
          @click="addOrderList"
        >
          加入購物車
        </button>
      </div>
    </div>
  </div>
</template>

<script>
// import { toRefs } from "vue";
import { onMounted, reactive } from "vue";
import { Modal } from "bootstrap";

export default {
  name: "OptionWindow",
  props: {
    sentData: Object,
  },
  setup() {
    // let orderNum = ref(1);
    let pickOption = reactive({
      whatCup: "",
      whatTemp: "",
      whatSugar: "",
    });
    let orderDesc = reactive({
      productName: "",
      whatCup: "",
      whatTemp: "",
      whatSugar: "",
      totalPrice: 0,
      orderNum: 1,
    });
    let ctrlModal = reactive({
      myModal: {},
    });

    onMounted(() => {
      ctrlModal.myModal = new Modal(document.getElementById("optionModal"));
      // console.log(ctrlModal.myModal);
      // ctrlModal.myModal.show();
    });
    return { pickOption, orderDesc, ctrlModal };
  },
  methods: {
    numMinus() {
      return this.orderDesc.orderNum == 1 ? "" : this.orderDesc.orderNum--;
    },
    numPlus() {
      return this.orderDesc.orderNum++;
    },
    totalMoney() {
      let price;
      if (this.orderDesc.whatCup == "mediumSize") {
        price = this.sentData.price_medium;
      } else {
        price = this.sentData.price_large;
      }
      return price * this.orderDesc.orderNum;
    },
    addOrderList() {
      switch (this.pickOption.whatCup) {
        case "largeSize":
          this.orderDesc.whatCup = "大杯";
          break;
        case "mediumSize":
          this.orderDesc.whatCup = "中杯";
          break;
        default:
          break;
      }
      switch (this.pickOption.whatTemp) {
        case "iceFull":
          this.orderDesc.whatTemp = "正常冰";
          break;
        case "iceFew":
          this.orderDesc.whatTemp = "少冰";
          break;
        case "iceLittle":
          this.orderDesc.whatTemp = "微冰";
          break;
        case "iceNone":
          this.orderDesc.whatTemp = "去冰";
          break;
        case "nomralTemp":
          this.orderDesc.whatTemp = "常溫";
          break;
        case "hotTemp":
          this.orderDesc.whatTemp = "熱";
          break;
        default:
          break;
      }
      switch (this.pickOption.whatSugar) {
        case "sugarFull":
          this.orderDesc.whatSugar = "正常糖";
          break;
        case "sugarFew":
          this.orderDesc.whatSugar = "少糖";
          break;
        case "sugarHalf":
          this.orderDesc.whatSugar = "半糖";
          break;
        case "sugarLittle":
          this.orderDesc.whatSugar = "微糖";
          break;
        case "sugarNone":
          this.orderDesc.whatSugar = "無糖";
          break;
        default:
          break;
      }
      this.orderDesc.productName = this.sentData.productName;
      this.orderDesc.totalPrice = this.totalMoney();
      this.$store.commit("addOrderCar", this.orderDesc);
      this.ctrlModal.myModal.hide();
    },
  },
};
</script>

<style lang="scss" scoped>
.optionBtn {
  width: 100px;
}
</style>